//MysqlMysql--indexNodeindexNode
import Config1 from "../indexNode2.js";//
import Config2 from "../indexNode1.js";//
let connection2;//
let connection1;//
import Router from "koa-router";
const Router2 = new Router();


(async ()=>{
  try{
    //Config,connection
    connection2 = await Config1();
    connection1 = await Config2();
    console.log("2");
    console.log("1");
  }
  catch(error){
    console.log("2");
    console.log("1");
  }
})();
// 
Router2.get("/t_device/first", async ctx => {
  try {
      let query;
      //  undefined 
      query = "SELECT * FROM t_container ORDER BY ctime DESC";
      //   `await` 
      const [results] = await connection2.execute(query);
      // 
      const formattedResults = results.map(row => [
          row.PID,
          row.VID,
          row.ctime
      ]);
      console.log("rows:"+results);
      ctx.set("Content-Type", "application/json");
      ctx.body = JSON.stringify(formattedResults);
  } 
  catch (error) {
      console.error(":", error);
  }

  //
//   res.send(
//     [
//       [
//         "2021",
//         "",
//         "1",
//         "2025-03-03T09:29:47.000Z"
//       ]
//     ]
//   );
});

// --
Router2.get("/t_device", async ctx => {
  try {
      let query;
      //  undefined 
      query = "SELECT * FROM t_device";
      //   `await` 
      const [results] = await connection1.execute(query);
      // 
      console.log("rows:"+results);
      ctx.set("Content-Type", "application/json");
      ctx.body = JSON.stringify(formattedResults);
  } 
  catch (error) {
      console.error(":", error);
  }

  //
//   res.send(
//     [
//       [
//         "2021",
//         "",
//         "1",
//         "2025-03-03T09:29:47.000Z"
//       ]
//     ]
//   );
});


export default Router2;

