Router3.get("/t_error_msg/count",async ctx=>{
  const { start,end } = ctx.query;
  if(start==="1"&&end==="1"){
    const [rows] = await connection1.execute("SELECT * FROM t_error_history");//d_no
    ctx.body = ""+rows.length;
  }
  else if(start==="end"&&end!=="1"){
    const formatTime = (timeStr) => new Date(timeStr).toISOString().slice(0, 19).replace('T', ' ');
    const formattedEnd = formatTime(end);
    const [rows] = await connection1.execute(`SELECT * FROM t_error_history WHERE c_time<'${formattedEnd}'`);//d_no
    ctx.body = ""+rows.length;
  }
  else{
    const formatTime = (timeStr) => new Date(timeStr).toISOString().slice(0, 19).replace('T', ' ');
    const formattedStart = formatTime(start);
    const formattedEnd = formatTime(end);
    const [rows] = await connection1.execute(`SELECT * FROM t_error_history WHERE c_time BETWEEN '${formattedStart}' AND '${formattedEnd}'`);//d_no
    ctx.body = ""+rows.length;
  }
})


